#skill-main {
    padding-top: 20px;
    padding-bottom: 20px;
    display: flex;
    justify-content: center;
}
.skill-col {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    padding-left: 5px;
    padding-right: 5px;
    padding-bottom: 30px;
    position: relative;
    overflow: hidden;
    z-index: 10;
}

.skill-col h4 {
    margin: 0;
    padding-bottom: 10px;
}

.skill-row {
    flex-basis: 10%;
    width: 10%;
    text-align: center;
}

//Media queries 1024px - desktop size
@media only screen and (max-width: 1200px) {
    .skill-row {
        flex-basis: 25%;
        width: 10%;
        text-align: center;
    }
}

//Media queries 1024px - desktop size
@media only screen and (max-width: 500px) {
    .skill-row {
        flex-basis: 50%;
        width: 10%;
        text-align: center;
    }
}

